@font-face {
  font-family: "GimhaeGayaR";
  src: url("https://fastly.jsdelivr.net/gh/projectnoonnu/noonfonts_2202-2@1.0/GimhaeGayaR.woff")
    format("woff");
  font-weight: normal;
  font-style: normal;
}

#footer {
  min-width: 1200px;
  padding: 40px 0 50px 0;
  background-color: rgba(55, 55, 55, 1);

  display: flex;
  justify-content: center;
}

.footer-container {
  width: 1200px;
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.footer-top {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.footer-logo-box {
  display: flex;
  align-items: center;
  gap: 10px;
}

.footer-logo-text {
  color: rgba(255, 255, 255, 1);
  font-family: "GimhaeGayaR", sans-serif;
  font-size: 20px;
  font-weight: 400;
  line-height: 140%;
  letter-spacing: -0.4px;
}

.footer-link-box {
  display: flex;
  align-items: center;
  gap: 20px;
}

.footer-email-link {
  color: rgba(255, 255, 255, 1);

  font-size: 12px;
  font-weight: 400;
}

.footer-bottom {
  color: rgba(255, 255, 255, 1);

  font-size: 12px;
  font-weight: 400;
}

.footer-copyright {
  color: rgba(255, 255, 255, 1);

  font-size: 12px;
  font-weight: 400;
}
